import React, { useEffect, useRef, useState, useCallback } from "react";
import { Pagination, Select, message } from "antd";
import { getAgent977Report } from "../../api/login/Login";
import Loader from "../../common/loader/Loader";
import { FaEye } from "react-icons/fa";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Table from "react-bootstrap/Table";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

const IrctcReport = () => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [selectedRow, setSelectedRow] = useState(null);

  const [count, setCount] = useState(10);
  const [page, setPage] = useState(0);
  const [totalCount, setTotalCount] = useState(0);

  const effectRun = useRef(false);

  /* ðŸ”¥ DATE ALWAYS EMPTY */
  const [filters, setFilters] = useState({
    startDate: "",
    endDate: "",
    search: "",
  });
  const statusPillStyle = {
    display: "inline-flex",
    alignItems: "center",
    justifyContent: "center",
    minWidth: "160px",
    height: "30px",
    padding: "0 12px",
    borderRadius: "999px",
    fontSize: "12px",
    fontWeight: 600,
    color: "#fff",
    textAlign: "center",
    whiteSpace: "nowrap",
  };

  const paymentStatusStyle = (status) => {
    switch (status) {
      case "SUCCESS":
        return { bg: "#16a34a", text: "Success" };

      case "PROCESSING":
      case "PENDING":
        return { bg: "#f59e0b", text: status.replace("_", " ") };

      case "REVERSED":
        return { bg: "#2563eb", text: "Reversed" };

      default:
        return { bg: "#6b7280", text: status || "-" };
    }
  };

  const bookingStatusStyle = (status) => {
    switch (status) {
      case "BOOKED":
        return { bg: "#16a34a", text: "Booked" };

      case "BOOKING_IN_PROGRESS":
        return { bg: "#f59e0b", text: "Booking In Progress" };

      case "PAYMENT_DEBITED":
        return { bg: "#0ea5e9", text: "Payment Debited" };

      case "REFUNDED":
        return { bg: "#2563eb", text: "Refunded" };

      default:
        return { bg: "#6b7280", text: status || "-" };
    }
  };


 const handleFilterChange = (e) => {
    const { name, value } = e.target;

    setFilters((prev) => {
      if (name === "startDate" && prev.endDate && value > prev.endDate) {
        return {
          ...prev,
          startDate: value,
          endDate: value,
        };
      }

      return {
        ...prev,
        [name]: value,
      };
    });
  };

  /* ================= FETCH DATA ================= */
  const fetchTransactions = useCallback(
    async (pageNo = page, limitNo = count, appliedFilters = filters) => {
      setLoading(true);
      try {
        const res = await getAgent977Report({
          page: pageNo + 1,     // API is 1-based
          limit: limitNo,      // âœ… use passed page size
          ...appliedFilters,
        });

        setData(res?.data?.data || []);
        setTotalCount(res?.data?.totalRecords || 0);
      } catch (error) {
        message.error("Failed to fetch IRCTC transactions");
      }
      setLoading(false);
    },
    []
  );



  useEffect(() => {
    if (!effectRun.current) {
      fetchTransactions(page, count);
      effectRun.current = true;
    }
  }, []);

  /* ================= HANDLERS ================= */
  const handleSearch = () => {
    if (filters.startDate && filters.endDate && filters.endDate < filters.startDate) {
      return message.error("End date cannot be earlier than start date");
    }
    setPage(0);
    fetchTransactions(0, count, filters);
  };

  const handleReset = () => {
    const reset = { startDate: "", endDate: "", search: "" };
    setFilters(reset);
    setPage(0);
    fetchTransactions(0, count, reset); // pass fresh cleared filters
  };


  const handlePageSizeChange = (value) => {
    setCount(value);
    setPage(0);
    fetchTransactions(0, value);
  };





  const prepareExcelDownload = () => {
    if (!data.length) return message.error("No data found");
    setTimeout(() => document.getElementById("excelExportIrctc").click(), 200);
  };

  return (
    <>
      {loading && <Loader />}

      {/* PAGE HEADING */}
      <div className="PageHeading" style={{ color: "var(--bs-primary3)" }}>
        <h1>IRCTC Report</h1>
      </div>

      <div className="ContentArea">

        {/* FILTER CARD */}
        <div
          className="card"
          style={{
            backgroundColor: "var(--bs-primary2)",
            border: "1px solid var(--bs-nav)",
            color: "var(--bs-primary3)",
          }}
        >
          <div className="card-body">
            <div className="form-row" style={{ alignItems: "end" }}>

              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  className="form-control input-white-bg"
                  name="startDate"
                  value={filters.startDate}
                  onChange={handleFilterChange}
                />
              </div>

              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  className="form-control input-white-bg"
                  name="endDate"
                  value={filters.endDate}
                  onChange={handleFilterChange}
                  min={filters.startDate || undefined}
   onBlur={() => {
                    if (
                      filters.startDate &&
                      filters.endDate &&
                      filters.endDate < filters.startDate
                    ) {
                      message.error(
                        "End date cannot be earlier than Start date",
                      );
                      setFilters((prev) => ({
                        ...prev,
                        endDate: prev.startDate,
                      }));
                    }
                  }}
                />
              </div>

              <div className="form-group col-md-4">
                <label>Txn ID / Reservation ID</label>
                <input
                  type="text"
                  className="form-control bs-autofill-fix"
                  name="search"
                  placeholder="Search..."
                  value={filters.search}
                  onChange={handleFilterChange}
                  style={{
                    backgroundColor: "var(--bs-primary2)",
                    color: "var(--bs-primary3)",
                    border: "1px solid var(--bs-nav)",
                  }}
                />
              </div>

              <div className="form-group col-md-2 mt-3">
                <button
                  className="btn"
                  onClick={handleSearch}
                  style={{
                    backgroundColor: "#6C757D",
                    color: "#FFF",
                    border: "1px solid var(--bs-nav)",
                  }}
                >
                  Search
                </button>

                <button
                  className="btn ml-2"
                  onClick={handleReset}
                  style={{
                    backgroundColor: "#F8F9FA",
                    color: "var(--bs-primary2)",
                    border: "1px solid var(--bs-primary3)",
                  }}
                >
                  Reset
                </button>
              </div>

            </div>
          </div>
        </div>

        {/* TABLE CARD */}
        <div
          className="card mt-4"
          style={{
            backgroundColor: "var(--bs-primary2)",
            border: "1px solid var(--bs-nav)",
            overflow: "auto",
          }}
        >
          <div className="card-body">

            {/* EXCEL BUTTON */}
            <div className="d-flex justify-content-end">
              <Button
                onClick={prepareExcelDownload}
                style={{
                  backgroundColor: "#E8DAD6",
                  color: "var(--bs-primary2)",
                  border: "1px solid var(--bs-nav)",
                }}
              >
                Download Excel
              </Button>

              <ReactHTMLTableToExcel
                id="excelExportIrctc"
                filename="IRCTC_Agent977_Report"
                sheet="Sheet1"
                className="d-none"
                table="tableIrctc"
              />
            </div>

            {/* TABLE */}
            <table
              className="table table-bordered table-hover mt-3"
              id="tableIrctc"
              style={{ borderColor: "var(--bs-nav)", whiteSpace: "nowrap" }}
            >
              <thead
                style={{
                  backgroundColor: "var(--bs-primary1)",
                  color: "var(--bs-primary2)",
                }}
              >
                <tr>
                  <th>S No</th>
                  <th>Date</th>
                  <th>Txn ID</th>
                  <th>Reservation ID</th>
                  <th>PNR</th>
                  <th>Agent User</th>
                  <th>Class</th>
                  <th>Pax</th>
                  <th>Payment Mode</th>
                  <th>Amount</th>
                  <th>Agent Fee inclGST</th>
                  <th>Pg Charge inclGST </th>
                  <th>Total Amount</th>
                  <th>Payment Status</th>
                  <th>Booking Status</th>
                  <th>View</th>
                </tr>
              </thead>

              <tbody style={{ color: "var(--bs-primary3)" }}>
                {data.length ? (
                  data.map((item, i) => (
                    <tr key={item?._id}>
                      <td>{i + 1}</td>
                      <td>{new Date(item?.createdAt).toLocaleString()}</td>
                      <td>{item?.txn_id}</td>
                      <td className="text-center">{item?.supplierRef || "-"}</td>
                      <td>{item?.pnr}</td>
                      <td>{item?.agentUserId}</td>
                      <td>{item?.classType}</td>
                      <td>{item?.noOfPax || "-"}</td>
                      <td>{item?.paymentMode || "-"}</td>
                      <td>â‚¹{item?.txnAmount}</td>
                      <td>â‚¹{item?.maxAgentFeeinclGST}</td>
                      <td>â‚¹{item?.maxPGChargeinclGST}</td>
                      <td>â‚¹{item?.ticketPrintRate}</td>
                      <td style={{ whiteSpace: "nowrap" }}>
                        {(() => {
                          const s = paymentStatusStyle(item?.paymentStatus);
                          return (
                            <span
                              style={{
                                ...statusPillStyle,
                                backgroundColor: s.bg,
                              }}
                            >
                              {s.text}
                            </span>
                          );
                        })()}
                      </td>


                      <td style={{ whiteSpace: "nowrap" }}>
                        {(() => {
                          const s = bookingStatusStyle(item?.ticketBookingStatus);
                          return (
                            <span
                              style={{
                                ...statusPillStyle,
                                backgroundColor: s.bg,
                              }}
                            >
                              {s.text}
                            </span>
                          );
                        })()}
                      </td>


                      <td>
                        <FaEye
                          onClick={() => setSelectedRow(item)}
                          style={{
                            cursor: "pointer",
                            color: "var(--bs-primary2)",
                          }}
                        />
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="13" className="text-center">
                      No Transaction Found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>

            {/* PAGINATION */}
            <div className="dataTables_info_page">
              <div className="d-flex align-items-center">
                <div className="dataTables_length me-3">
                  <label className="me-2">Show</label>
                  <Select
                    value={count}
                    onChange={handlePageSizeChange}
                    style={{ width: 120 }}
                    options={[
                      { value: 10, label: "10 / page" },
                      { value: 20, label: "20 / page" },
                      { value: 50, label: "50 / page" },
                      { value: 100, label: "100 / page" },
                    ]}
                  />
                </div>

                <div className="dataTables_info">
                  Total {totalCount} entries
                </div>
              </div>

              <div className="dataTables_paginate paging_simple_numbers">
                <Pagination
                  current={page + 1}
                  onChange={(p) => {
                    const newPage = p - 1;
                    setPage(newPage);
                    fetchTransactions(newPage, count, filters);
                  }}
                  total={totalCount}
                  pageSize={count}
                  showSizeChanger={false}
                />


              </div>
            </div>

          </div>
        </div>
      </div>

      {/* MODAL */}
      {selectedRow && (
        <Modal show centered onHide={() => setSelectedRow(null)}>
          <Modal.Header
            closeButton
            style={{
              backgroundColor: "var(--bs-primary1)",
              color: "var(--bs-primary2)",
            }}
          >
            <Modal.Title>
              Transaction #{selectedRow.txn_id}
            </Modal.Title>
          </Modal.Header>

          <Modal.Body
            style={{
              backgroundColor: "var(--bs-primary1)",
              color: "var(--bs-primary3)",
            }}
          >
            <Table borderless>
              <tbody>
                <tr><td>Reservation ID</td><td>{selectedRow?.reservationId}</td></tr>
                <tr><td>Merchant Code</td><td>{selectedRow?.merchantCode}</td></tr>
                <tr><td>Agent User</td><td>{selectedRow?.agentUserId}</td></tr>
                <tr><td>Total Amount</td><td>â‚¹{selectedRow?.ticketPrintRate}</td></tr>
                <tr><td>Payment Status</td><td>{selectedRow?.paymentStatus}</td></tr>
                <tr><td>Booking Status</td><td>{selectedRow?.ticketBookingStatus}</td></tr>
              </tbody>
            </Table>
          </Modal.Body>

          <Modal.Footer
            style={{
              backgroundColor: "var(--bs-primary2)",
            }}
          >
            <Button
              onClick={() => setSelectedRow(null)}
              style={{
                backgroundColor: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                border: "1px solid var(--bs-nav)",
              }}
            >
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
};

export default IrctcReport;
